@import url(https://getratha.netlify.app/styles.css);
.filter {
  position: relative;
}

.filter-container {
  position: fixed;
  top: 3rem;
  width: 13rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 100vh;
  margin: 0.1rem;
  border: 2px solid var(--gray-300);
}

.filter-header-container {
  display: flex;
  width: 10rem;
  justify-content: space-between;
  align-items: center;
}

.filter-header {
  padding: 1rem 1rem;
}

.filter-clear {
  position: relative;
  top: -5px;
  background-color: transparent;
  text-decoration: underline;
}

.slider-container {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}

.slider {
  width: auto;
  height: 5px;
  cursor: pointer;
  -webkit-appearance: none;
          appearance: none;
  background-color: var(--gray-400);
  border-radius: 1rem;
  box-shadow: inset 0 0 10px var(--gray-300);
}

.price-slider-container,
.catagory-container,
.rating-container,
.sortby-container {
  padding: 0.5rem 1rem;
}

.catagory-box,
.rating-radio-btn,
.sortby-radio-btn {
  margin: 0.2rem 0.5rem;
}

.navigation {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}

.nav-logo-style {
  font-family: fantasy;
  color: var(--primary-color);
}

.main-container {
  padding: 1rem 5%;
  margin: 3rem 0.5rem;
}

.cust-card {
  width: 100%;
  height: 10vw;
  padding: 2rem 1.5rem;
  justify-content: center;
  position: relative;
}

.card-title {
  background-color: var(--primary-hover);
}

.home-img-card {
  width: 100%;
  height: 50vw;
  padding: 4rem 1.5rem;
}

.home-img-card > a {
  width: 100%;
}

.card {
  width: 50%;
}

.card__primary-action {
  margin: 1.5rem 1rem;
}

.card__media-row-resp {
  width: 15vw;
  height: 10vw;
}

.product-link {
  color: var(--secondary-color);
}

@media screen and (max-width: 700px) {
  .main-container {
    padding: 0.5rem;
  }

  .cust-card {
    width: 100%;
    height: 100%;
    padding: 0.5rem 0.2rem;
  }

  .home-img-card {
    width: 100%;
    height: 70vw;
    padding: 1rem 0.2rem;
  }

  .flex {
    flex-direction: column;
  }

  .card {
    width: 100%;
  }

  .card__media-row-resp {
    width: 30vw;
    height: 25vw;
  }
}

.login-header {
  font-weight: 900;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.link-forgot {
  display: flex;
  justify-content: end;
}

.link-forgot a {
  color: var(--blue);
  font-size: small;
  font-weight: 600;
  margin-bottom: 1rem;
}

.link-create {
  display: flex;
  margin: 1rem;
  justify-content: center;
  font-weight: 600;
  color: var(--dark);
}

.link-create:hover {
  text-decoration: underline;
}

.my-cart {
  position: relative;
  top: 4rem;
  text-align: center;
  margin: 1rem;
}

.cart-main {
  display: flex;
  flex-wrap: wrap;
  grid-row-gap: 2rem;
  row-gap: 2rem;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  justify-content: space-around;
}

.card-horizontal {
  background-color: var(--gray-100);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.card__primary-action-horizontal {
  width: 30rem;
  max-width: 40rem;
  height: inherit;
  margin: 0;
}

.card__media-row-horizontal {
  width: 16rem;
  background-color: var(--gray-300);
}

.card-img-horizontal {
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.card__primary-horizontal {
  padding: 1rem 1rem 1rem 0.5rem;
  text-align: start;
  font-size: 0.8rem;
}

.card-price {
  font-size: 0.8rem;
  font-weight: 900;
  padding-bottom: 0.5rem;
}

.card-cut-price {
  text-decoration: line-through;
  opacity: 0.5;
}

.card-discount {
  font-size: 0.8rem;
  font-weight: 900;
  padding-bottom: 0.5rem;
  color: var(--gray-600);
}

.card-product-quantity {
  display: inline-flex;
  flex-direction: row;
  padding-bottom: 2rem;
}

.product-quantity-decrease,
.product-quantity-increase {
  font-size: 1rem;
  font-weight: 900;
  background-color: transparent;
  border-radius: 50%;
  border: 2px solid var(--dark);
  padding: 0 3px;
}

.product-quantity-count {
  text-align: center;
  width: 2rem;
}

.cart-billing-container {
  width: 17rem;
  background-color: var(--gray-100);
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

.billing-container,
.cart-billing-total {
  padding: 0.5rem 0;
}

.cart-billing-total,
.billing-prices,
.billing-discount-price,
.billing-delivery-charges {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.3rem;
}

.main {
  margin-top: 3rem;
  margin-left: 13rem;
  padding: 1rem;
  background-color: var(--gray-100);
}

.main-product-count {
  padding: 0.2rem 0.5rem;
}

.product-container {
  display: flex;
  flex-wrap: wrap;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  justify-content: space-evenly;
}

.card-vertical {
  margin: 0;
  width: 15rem;
  min-width: 12rem;
  padding: 0.1rem;
  background-color: var(--white);
}

.card__primary-action-vertical {
  margin: 0;
}

.card-badge-vertical {
  color: inherit;
  background-color: var(--primary-color);
  border-radius: 50%;
  padding: 0.1rem;
  border: 1px solid var(--gray-300);
  cursor: pointer;
}

.card-badge-vertical:hover {
  color: var(--danger);
  background-color: inherit;
}

.card__media-column-vertical {
  padding: 1rem;
  width: inherit;
  height: 42vh;
  background-color: var(--gray-300);
}

.card__secondary-vertical {
  padding-bottom: 0;
}

.card__subtitle {
  padding-top: 0.5rem;
  margin: 0;
}

.card__secondary-vertical {
  padding: 0.5rem 0;
}

.card-img-height {
  width: 100%;
  height: 38vh;
}

.navigation {
  grid-area: header;
}
.main {
  grid-area: main;
}
.filter {
  grid-area: sidebar;
}

.product-page {
  display: grid;
  grid-template-columns: 13rem auto;
  grid-template-rows: auto;
  grid-template-areas:
    "header header header header"
    "sidebar main main main";
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
}

.wishlist-container {
  margin-top: 3rem;
  padding: 1rem;
  background-color: var(--gray-100);
}

.btn-full {
  width: 100%;
  padding: 0.5rem;
  margin: 0;
}

.btn-wishlist {
  background-color: var(--gray-600);
}

.btn-wishlist:hover {
  color: var(--primary-color);
  background-color: var(--gray-700);
  border: none;
}

.card-badge-check {
  color: var(--danger);
}

.home-header {
  position: relative;
  top: 15vh;
  margin: 0 10vw;
  text-align: center;
}

.flex {
  display: flex;
}
.center {
  justify-content: center;
}
.space-around {
  justify-content: space-around;
}
.flex-row {
  flex-direction: row;
}
.flex-column {
  flex-direction: column;
}

.ul-flex-center {
  display: flex;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.li-center {
  justify-content: center;
}

.header-init {
  color: var(--primary-hover);
}

.button-cust {
  padding: 0.1rem 0.06rem;
  margin: 0.3rem 0.2rem;
}

.img-container {
  width: 300px;
  height: 300px;
  margin: auto;
}

.list-container {
  padding: auto;
}

br {
  margin: 1rem;
}

@media screen and (max-width: 600px) {
  .home-header {
    top: 10vh;
    margin: 0 5vw;
  }
}

* {
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

button,
a {
  cursor: pointer;
}

@media screen and (max-width: 700px) {
  * {
    padding: 0;
  }
}

