.main {
  margin-top: 3rem;
  margin-left: 13rem;
  padding: 1rem;
  background-color: var(--gray-100);
}

.main-product-count {
  padding: 0.2rem 0.5rem;
}

.product-container {
  display: flex;
  flex-wrap: wrap;
  row-gap: 1rem;
  justify-content: space-evenly;
}

.card-vertical {
  margin: 0;
  width: 15rem;
  min-width: 12rem;
  padding: 0.1rem;
  background-color: var(--white);
}

.card__primary-action-vertical {
  margin: 0;
}

.card-badge-vertical {
  color: inherit;
  background-color: var(--primary-color);
  border-radius: 50%;
  padding: 0.1rem;
  border: 1px solid var(--gray-300);
  cursor: pointer;
}

.card-badge-vertical:hover {
  color: var(--danger);
  background-color: inherit;
}

.card__media-column-vertical {
  padding: 1rem;
  width: inherit;
  height: 42vh;
  background-color: var(--gray-300);
}

.card__secondary-vertical {
  padding-bottom: 0;
}

.card__subtitle {
  padding-top: 0.5rem;
  margin: 0;
}

.card__secondary-vertical {
  padding: 0.5rem 0;
}

.card-img-height {
  width: 100%;
  height: 38vh;
}
