.my-cart {
  position: relative;
  top: 4rem;
  text-align: center;
  margin: 1rem;
}

.cart-main {
  display: flex;
  flex-wrap: wrap;
  row-gap: 2rem;
  column-gap: 1rem;
  justify-content: space-around;
}

.card-horizontal {
  background-color: var(--gray-100);
  width: fit-content;
}

.card__primary-action-horizontal {
  width: 30rem;
  max-width: 40rem;
  height: inherit;
  margin: 0;
}

.card__media-row-horizontal {
  width: 16rem;
  background-color: var(--gray-300);
}

.card-img-horizontal {
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.card__primary-horizontal {
  padding: 1rem 1rem 1rem 0.5rem;
  text-align: start;
  font-size: 0.8rem;
}

.card-price {
  font-size: 0.8rem;
  font-weight: 900;
  padding-bottom: 0.5rem;
}

.card-cut-price {
  text-decoration: line-through;
  opacity: 0.5;
}

.card-discount {
  font-size: 0.8rem;
  font-weight: 900;
  padding-bottom: 0.5rem;
  color: var(--gray-600);
}

.card-product-quantity {
  display: inline-flex;
  flex-direction: row;
  padding-bottom: 2rem;
}

.product-quantity-decrease,
.product-quantity-increase {
  font-size: 1rem;
  font-weight: 900;
  background-color: transparent;
  border-radius: 50%;
  border: 2px solid var(--dark);
  padding: 0 3px;
}

.product-quantity-count {
  text-align: center;
  width: 2rem;
}

.cart-billing-container {
  width: 17rem;
  background-color: var(--gray-100);
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

.billing-container,
.cart-billing-total {
  padding: 0.5rem 0;
}

.cart-billing-total,
.billing-prices,
.billing-discount-price,
.billing-delivery-charges {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.3rem;
}
