.filter {
  position: relative;
}

.filter-container {
  position: fixed;
  top: 3rem;
  width: 13rem;
  height: fit-content;
  min-height: 100vh;
  margin: 0.1rem;
  border: 2px solid var(--gray-300);
}

.filter-header-container {
  display: flex;
  width: 10rem;
  justify-content: space-between;
  align-items: center;
}

.filter-header {
  padding: 1rem 1rem;
}

.filter-clear {
  position: relative;
  top: -5px;
  background-color: transparent;
  text-decoration: underline;
}

.slider-container {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.slider {
  width: auto;
  height: 5px;
  cursor: pointer;
  appearance: none;
  background-color: var(--gray-400);
  border-radius: 1rem;
  box-shadow: inset 0 0 10px var(--gray-300);
}

.price-slider-container,
.catagory-container,
.rating-container,
.sortby-container {
  padding: 0.5rem 1rem;
}

.catagory-box,
.rating-radio-btn,
.sortby-radio-btn {
  margin: 0.2rem 0.5rem;
}
